*{
      margin:0;
      padding:0;
}

.container{
      width: 80%;
      display: flex;
      justify-content: center;
      flex-direction: column;
      margin: 20px auto;
      align-items: center;
}

.header_title{
      font-size: 2.5vw;
      font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
      color: white;
      background-color: rgb(185, 132, 235);
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 10px;
}

.header_sec_title{
      font-size: 16px;
      margin-top: 30px;
      color: rgb(201, 16, 16);
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.area{
      font-size: 16px;
      margin-top: 30px;
      color: rgb(201, 16, 16);
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
      display: none;
}
.areahide{
      font-size: 16px;
      margin-top: 30px;
      color: rgb(201, 16, 16);
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.areahide.active{
      font-size: 16px;
      margin-top: 30px;
      color: rgb(201, 16, 16);
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
      display: none;

}
.area.active{
      display: block;
}
.header_sec_title.active{
      display: none;
}

.file_select{
      height: 50px;
      width:100%;
      display: flex;
      justify-content: center;
      align-items: center;
}

input[type="file"]{
      width: 30%;
      height: 50%;
      border-radius: 25px ;
      font-size: 1.2vw;
      border: 1px solid gray;
      background-color: rgb(230, 230, 230);
}
.dawnlaod{
      border:1px solid green;
      background-color: green;
      text-decoration: none;
      color: rgb(255, 255, 255);
      font-size: 20px;
      padding: 2px;
      font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.crop_image_holder{
      display: flex;
      flex-direction: column;
      margin: 10px;

}
.cropButton{
      width: 25%;
      margin: 10px auto;
      height: 40px;
      font-size: 16px;
      background-color: rgb(241, 48, 48);
      color: white;
      border: none;
}
.coordinates{
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      color: rgb(102, 102, 102);
      margin: 0 auto;
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;

      
}

.coordinates.active{
display: none;
}
.result_image{
      width: 100%;
      height: 100%;
      margin: 10px auto;
}

.result_image img{
     height: 100%;
     width: 100%;
}


::-webkit-file-upload-button{
      width: 42%;
      border: 2px solid rgb(185, 132, 235);
      height:100%;
      border-radius: 25px;
      font-size: 1.3vw;
      background-color: rgb(185, 132, 235);
      color: white;
}


